.workflow-container {
  padding: 2rem;
  /* background-color: #f8f9fa; */
  min-height: 100vh;
}

/* Header Styles */
.workflow-header {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.workflow-title {
  color: #5F646D;
  font-weight: 700;
  font-size: 2rem;
  margin: 0;
}

/* Search Section */
.search-section {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.search-input-wrapper {
  position: relative;
}

.search-icon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #6c757d;
  z-index: 10;
}

.search-input {
  padding-left: 40px;
  border-radius: 8px;
  border: 2px solid #e9ecef;
  transition: all 0.3s ease;
}

.search-input:focus {
  border-color: #B68A35;
  box-shadow: 0 0 0 0.2rem rgba(212, 170, 58, 0.25);
}

/* Form Controls Focus States */
.form-control,
.form-select {
  border: 2px solid #e9ecef;
  border-radius: 8px;
  transition: all 0.3s ease;
  background-color: #fff;
}

.form-control:hover,
.form-select:hover {
  box-shadow: 0 0 0 0.1rem rgba(212, 170, 58, 0.15);
}

.form-control:focus,
.form-select:focus {
  box-shadow: 0 0 0 0.2rem rgba(212, 170, 58, 0.25);
  outline: none;
}

.form-control:focus-visible,
.form-select:focus-visible {
  box-shadow: 0 0 0 0.2rem rgba(212, 170, 58, 0.25);
  outline-offset: 2px;
}

/* Dropdown Selection States */
.form-control option:checked {
  color: white;
}


/* Selected option styling */


.form-control:not([value=""]) option:first-child {
  color: #6c757d;
  font-style: italic;
}

/* Dropdown arrow styling */


/* RTL support for dropdown arrow */
[dir="rtl"] .form-control {
  background-position: left 0.75rem center;
  padding-right: 0.75rem;
  padding-left: 2.5rem;
}

/* Focus state for dropdown */


/* Table Styles */
.table {
  margin-bottom: 0;
  font-size: 0.875rem; /* Base font size for table */
}

.table th {
  background-color: #f8f9fa;
  border-bottom: 2px solid #dee2e6;
  font-weight: 600;
  color: #495057;
  padding: 0.75rem 0.5rem;
  font-size: 0.8rem; /* Smaller header font */
}

.table td {
  padding: 0.75rem 0.5rem;
  vertical-align: middle;
  border-bottom: 1px solid #f1f3f4;
  font-size: 0.8rem; /* Smaller cell font */
}

.table tbody tr:hover {
  background-color: #f8f9fa;
}

/* Badge Styles */
.badge {
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.5rem 0.75rem;
  border-radius: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Action Type Badges */
.bg-success {
  background: linear-gradient(135deg, #C8E6C9 0%, #A5D6A7 100%) !important;
  color: #2E7D32 !important;
  border: 1px solid #A5D6A7;
}

.bg-info {
  background: linear-gradient(135deg, #E6D7A2 0%, #D4AF5F 100%) !important;
  color: #7C5E24 !important;
  border: 1px solid #D4AF5F;
}

.bg-danger {
  background: linear-gradient(135deg, #F9C8CD 0%, #F5B7C1 100%) !important;
  color: #C62828 !important;
  border: 1px solid #F5B7C1;
}

.bg-warning {
  background: linear-gradient(135deg, #E6D7A2 0%, #D4AA3A 100%) !important;
  color: #7C5E24 !important;
  border: 1px solid #D4AA3A;
}

.bg-primary {
  background: linear-gradient(135deg, #D4AF5F 0%, #D4AA3A 100%) !important;
  color: #7C5E24 !important;
  border: 1px solid #D4AA3A;
}

.bg-secondary {
  background: linear-gradient(135deg, #E6D7A2 0%, #D4AF5F 100%) !important;
  color: #7C5E24 !important;
  border: 1px solid #D4AF5F;
}

.bg-dark {
  background: linear-gradient(135deg, #7C5E24 0%, #5D4A1C 100%) !important;
  color: #E6D7A2 !important;
  border: 1px solid #5D4A1C;
}

/* Pagination Styles */
.pagination-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.pagination-info {
  text-align: center;
  color: #6b7280;
  font-size: 0.875rem;
  font-weight: 500;
}

.pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.page-size-control {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.page-size-control label {
  color: #374151;
  font-weight: 500;
  font-size: 0.875rem;
}

.page-size-control select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
}

.pagination {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 0.25rem;
}

.page-item {
  margin: 0;
}

.page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
  height: 40px;
  padding: 0.5rem;
  border: 1px solid #dee2e6;
  background: white;
  color: #D4AF5F;
  text-decoration: none;
  border-radius: 6px;
  transition: all 0.2s ease;
  cursor: pointer;
}

.page-link:hover {
  background: #E6D7A2;
  border-color: #D4AF5F;
  color: #7C5E24;
}

.page-item.active .page-link {
  background: #D4AF5F;
  border-color: #D4AF5F;
  color: #7C5E24;
}

.page-item.disabled .page-link {
  background: #f8f9fa;
  border-color: #dee2e6;
  color: #6c757d;
  cursor: not-allowed;
}

/* Button Styles */
.btn {
  border-radius: 8px;
  font-weight: 700;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  transition: all 0.2s ease;
}

.btn-primary {
  background-color: #D4AF5F;
  border-color: #D4AF5F;
  color: #7C5E24;
}

.btn-primary:hover {
  background-color: #D4AA3A;
  border-color: #D4AA3A;
  color: #7C5E24;
}

.btn-outline-secondary {
  color: #7C5E24;
  border-color: #D4AF5F;
}

.btn-outline-secondary:hover {
  background-color: #D4AF5F;
  border-color: #D4AF5F;
  color: #7C5E24;
}

/* Loading State */
.loading-state {
  text-align: center;
  padding: 3rem 0;
}

.spinner-border {
  width: 3rem;
  height: 3rem;
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem 2rem;
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.empty-state-icon {
  color: #dee2e6;
  font-size: 4rem;
  margin-bottom: 1rem;
}

.empty-state-title {
  color: #495057;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.empty-state-message {
  color: #6c757d;
  font-size: 1.1rem;
}

/* Workflow Description */
.workflow-description {
  max-width: 200px;
  word-wrap: break-word;
}

.workflow-description .text-truncate {
  font-size: 0.75rem; /* Even smaller font for description */
  line-height: 1.2;
  max-width: 180px;
}

/* User column styling */
.table td:nth-child(6) {
  max-width: 120px;
  font-size: 0.75rem;
}

.table td:nth-child(6) .fw-bold {
  font-size: 0.75rem;
  line-height: 1.2;
}

/* Description column styling */
.table td:nth-child(5) {
  max-width: 200px;
  font-size: 0.75rem;
}

/* Card Styles */
.card {
  border: 1px solid #dee2e6;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.card-header {
  background-color: #f8f9fa;
  border-bottom: 1px solid #dee2e6;
  font-weight: 600;
  padding: 1.5rem;
}

.card-body {
  padding: 0;
}

.card-footer {
  background-color: #f8f9fa;
  border-top: 1px solid #dee2e6;
  padding: 1.5rem;
}

/* RTL Support */
[dir="rtl"] .search-icon {
  left: auto;
  right: 12px;
}

[dir="rtl"] .search-input {
  padding-left: 12px;
  padding-right: 40px;
}

[dir="rtl"] .pagination-controls {
  flex-direction: row-reverse;
}

[dir="rtl"] .me-2 {
  margin-right: 0 !important;
  margin-left: 0.5rem !important;
}

[dir="rtl"] .ms-2 {
  margin-left: 0 !important;
  margin-right: 0.5rem !important;
}

[dir="rtl"] .text-end {
  text-align: left !important;
}

[dir="rtl"] .text-start {
  text-align: right !important;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .workflow-description {
    max-width: 150px;
  }
  
  .workflow-description .text-truncate {
    max-width: 130px;
  }
  
  .table td:nth-child(5) {
    max-width: 150px;
  }
  
  .table td:nth-child(6) {
    max-width: 100px;
  }
}

@media (max-width: 992px) {
  .workflow-description {
    max-width: 120px;
  }
  
  .workflow-description .text-truncate {
    max-width: 100px;
  }
  
  .table td:nth-child(5) {
    max-width: 120px;
  }
  
  .table td:nth-child(6) {
    max-width: 80px;
  }
  
  .table th,
  .table td {
    padding: 0.5rem 0.25rem;
    font-size: 0.75rem;
  }
}

@media (max-width: 768px) {
  .workflow-container {
    padding: 1rem;
  }

  .workflow-header {
    padding: 1rem;
  }

  .workflow-title {
    font-size: 1.5rem;
  }

  .search-section {
    padding: 1rem;
  }

  .pagination-controls {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .page-size-control {
    justify-content: center;
  }

  .workflow-description {
    max-width: 100px;
  }
  
  .workflow-description .text-truncate {
    max-width: 80px;
    font-size: 0.7rem;
  }

  .table-responsive {
    font-size: 0.75rem;
  }
  
  .table th,
  .table td {
    padding: 0.4rem 0.2rem;
    font-size: 0.7rem;
  }
  
  .table td:nth-child(5) {
    max-width: 100px;
  }
  
  .table td:nth-child(6) {
    max-width: 70px;
  }

  .d-flex.gap-2 {
    flex-direction: column;
    gap: 0.5rem !important;
  }

  .card-header .d-flex {
    flex-direction: column;
    align-items: flex-start !important;
    gap: 1rem;
  }
  
  .pagination {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .page-link {
    min-width: 32px;
    height: 32px;
    font-size: 0.75rem;
  }
}

@media (max-width: 576px) {
  .workflow-container {
    padding: 0.5rem;
  }
  
  .workflow-description {
    max-width: 80px;
  }
  
  .workflow-description .text-truncate {
    max-width: 60px;
    font-size: 0.65rem;
  }
  
  .table th,
  .table td {
    padding: 0.3rem 0.1rem;
    font-size: 0.65rem;
  }
  
  .table td:nth-child(5) {
    max-width: 80px;
  }
  
  .table td:nth-child(6) {
    max-width: 60px;
  }
  
  .pagination-controls {
    gap: 0.5rem;
  }
  
  .pagination-info {
    font-size: 0.75rem;
  }
}

/* Animation for smooth transitions */
.table tbody tr {
  transition: background-color 0.15s ease-in-out;
}

.btn {
  transition: all 0.15s ease-in-out;
}

/* Custom scrollbar for table */
.table-responsive::-webkit-scrollbar {
  height: 8px;
}

.table-responsive::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.table-responsive::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.table-responsive::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}
